.topbar {
  width: 100%;
  background: #121212;
  color: #fff;
  opacity: 0.8;
  &__wrapper {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
  &__left {
    display: flex;
    align-items: center;
  }
  &__item {
    margin: 0 0.5rem;
    display: flex;
    align-items: center;
    & p {
      font-size: 1.1rem;
      margin-left: 0.9rem;
      white-space: nowrap;
    }
    &:last-child {
      @include media-mobile {
        display: none;
      }
    }
  }
  &__social {
    height: 100%;
    display: flex;
    align-items: center;
    & ul {
      width: 100%;
      display: flex;
      justify-content: space-between;
    }
    & li {
      width: 2.3rem;
      height: 2.3rem;
      display: flex;
      justify-content: center;
      margin: 0 0.3rem;
      list-style: none;
      & a {
        width: 100%;
        height: 100%;
        text-decoration: none;
        color: #ffffff;
        background-position: center;
      }
    }
    &__facebook {
      background: url(./assets/img/facebook.png) no-repeat;
    }
    &__twitter {
      background: url(./assets/img/twitter.png) no-repeat;
    }
    &__instagram {
      background: url(./assets/img/instagram.png) no-repeat;
    }
    &__noname {
      background: url(./assets/img/noname.png) no-repeat;
    }
  }
  @include media-finish {
    display: none;
  }
}

.navigation {
  width: 100%;
  padding: 1.5rem 0 2rem 0;
  &__wrapper {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
  &__logo {
    height: 100%;
    font-family: "Playfair Display SC", "sans-serif";
    font-style: normal;
    font-weight: normal;
    display: flex;
    justify-content: center;
    align-items: center;
    & a {
      font-size: 2.4rem;
      color: #121212;
      text-decoration: none;
    }
  }

  &__user {
    height: 3rem;
    display: flex;
    align-items: center;
    padding: 0 0.5rem 0 0;
    & ul {
      width: 100%;
      display: flex;
      justify-content: space-between;
    }
    & li {
      width: 3rem;
      height: 1.8rem;
      display: flex;
      justify-content: center;
      margin: 0 0.3rem;
      list-style: none;
      & a {
        width: 100%;
        height: 100%;
        text-decoration: none;
        color: #ffffff;
        background-position: center;
        background-size: contain;
      }
      &:last-child {
        display: none;
        @include media-tablet {
          display: block;
          margin-left: 1rem;
        }
      }
    }
    &__search {
      background: url(./assets/img/search.png) no-repeat;
    }
    &__planet {
      background: url(./assets/img/planet.png) no-repeat;
    }
    &__person {
      background: url(./assets/img/person.png) no-repeat;
    }
    &__basket {
      background: url(./assets/img/basket.png) no-repeat;
      position: relative;
      & div {
        width: 65%;
        height: 100%;
        font-size: 0.9rem;
        font-weight: bold;
        position: absolute;
        top: -0.8rem;
        right: -0.5rem;
        border-radius: 50%;
        border: 0.2rem solid #ffffff;
        color: #fff;
        background-color: #121212;
        display: flex;
        justify-content: center;
        align-items: center;
      }
    }
  }
}

.menu {
  display: flex;
  justify-content: space-between;
  align-items: center;
  list-style: none;
  font-size: 1.4rem;
  transition: all 0.5s;
  & li {
    margin: 0 1.4rem;
    font-size: 1.4rem;
    line-height: 16px;
    & a {
      font-size: 1.4rem;
      @include media-tablet {
        font-size: 2rem;
      }
    }
    @include media-tablet {
      line-height: 4.4rem;
    }
  }
  @include media-tablet {
    width: 100%;
    height: 100vh;
    display: block;
    position: absolute;
    top: 0;
    left: 0;
    padding: 3rem 0 0 0;
    text-align: left;
    transform: translateX(-100%);
    z-index: 100;
    background-color: #fff;
  }
  @include media-finish {
    padding: 1rem 0 0 0;
  }
  &.visible {
    transform: translateX(0%);
  }
}
